* {
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff8ec;
}

main {
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  padding: 0 1rem;
  display: flex;
}

.profile {
  flex-direction: row;
  align-items: center;
  padding-left: .5rem;
  display: none;
}

h1, h2, h3, h4, h5, .buttons, .copyright, .form-ui {
  font-family: Inknut Antiqua, Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
}

h1 {
  color: #b59136;
  letter-spacing: 1px;
  margin: 1rem;
  font-size: 2.75rem;
}

h2 {
  color: #005b4c;
  font-size: 1.8rem;
}

.row {
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.col {
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

#header-data {
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.fa-user-astronaut {
  color: #005b4c;
  cursor: pointer;
  border: 3px solid #005b4c;
  border-radius: 50%;
  padding: .35rem;
  font-size: 1.5rem;
}

.user {
  margin: 1.5rem 1rem;
}

.logo {
  flex-direction: row;
  align-items: center;
  display: flex;
}

#logout-btn {
  display: none;
}

form {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.form-wrapper {
  width: 48rem;
  background-color: #fff;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: center;
  margin: 1rem;
  padding: 2rem;
  display: flex;
  box-shadow: 0 3px 8px #4a4453b3;
}

.account-title {
  color: #604200;
}

.form-ui {
  color: #604200;
  cursor: pointer;
  background-color: #fff;
  border-bottom: 3px solid #fff;
  font-size: 1rem;
}

.form-ui:hover {
  text-shadow: 3px 3px 5px #b59136;
  border-bottom: 3px solid #604200;
}

#message-form {
  margin: 1.5rem;
}

input {
  height: 2rem;
  border: 2px solid #b59136;
  border-radius: .5rem;
  margin: 1.5rem;
  padding: .5rem 1.5rem;
  transition-duration: .3s;
  box-shadow: 0 3px 8px #4a4453b3;
}

#username, #password {
  width: 15rem;
}

#message {
  width: 40rem;
}

.buttons {
  color: #fff;
  width: 6.5rem;
  height: 2.5rem;
  cursor: pointer;
  background-color: #b59136;
  border-radius: .5rem;
  margin: 1.5rem;
  font-size: 1.06rem;
  transition-duration: .3s;
  box-shadow: 0 3px 8px #4a4453b3;
}

.buttons:hover {
  background-color: #604200;
}

#forums-section {
  width: 100%;
  flex-direction: row;
  display: none;
}

#sidebar {
  width: 15%;
  margin-top: 10rem;
}

#sidebar a {
  color: #b59136;
  padding: .5rem 1rem;
  font-size: 1.5rem;
  text-decoration: none;
  display: block;
}

#sidebar a:hover {
  color: #604200;
}

#topics-section {
  width: 85%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

#travel-div {
  display: flex;
}

#sport-div, #gaming-div {
  display: none;
}

.topics {
  width: 80%;
  min-height: 30rem;
  background-color: #fff;
  border: 2px solid #005b4c;
  border-radius: .5rem;
  flex-direction: column;
  align-items: center;
  margin: 1rem 1rem 3rem;
  padding: 1rem;
  display: flex;
  box-shadow: 0 3px 8px #4a4453b3;
}

.chat-container {
  width: 100%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.message-container {
  width: 80%;
  border: 2px solid #005b4c;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: space-between;
  margin: .25rem;
  padding: .5rem 1.2rem;
  display: flex;
  box-shadow: 0 3px 8px #4a4453b3;
}

.message-container:hover {
  background-color: #ffeeca;
  border-color: #766031;
}

.user-div {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.mess-div {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 0 1rem;
  display: flex;
}

.delete-btn {
  color: #fff;
  width: 1.5rem;
  height: 1.5rem;
  background-color: #c1564b;
  border-radius: .4rem;
  padding: .3rem;
  font-size: .7rem;
  font-weight: bold;
  transition-duration: .3s;
  box-shadow: 0 3px 8px #4a4453b3;
}

.delete-btn:hover {
  color: #ff614c;
  background-color: #fff;
  border: 2px solid #ff614c;
}

.mess-edit:focus {
  background-color: #fff;
  border: 2px solid #ff614c;
  border-radius: .25rem;
  padding: .25rem .5rem;
  font-style: italic;
  font-weight: bold;
}

.footer {
  width: 100%;
  height: 4.2rem;
  background-color: #b59136;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-top: 1rem;
  font-size: 1.1rem;
  display: flex;
  box-shadow: 4px 0 8px #4a4453b3;
}

.copyright {
  color: #fff;
  padding: .5rem;
}

#chevron a {
  color: #fff;
  font-size: 2.3rem;
}

#chevron a:hover {
  text-shadow: 2px 3px 5px #4a4453b3;
}

